@keyframes fade-in {
  from { opacity: 0; }
  to   { opacity: 1; }
}

body {
	overflow-x: hidden;
	background-color: #fff;
	color: #222;
	font-size: calc(12px + 0.75vw);
	font-family: "Literata", serif; 
	margin: 0;
  animation: fade-in 1s;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Bree Serif", serif;
	margin: 1.5em 0 1.5em 0;
	text-transform: uppercase;
	text-shadow: 0 1px 3px rgba(0, 0, 0, 0.4);
}

h1 { 
	font-size: 1.69em; 
	line-height: calc(18px + 0.7em);
}
h2 { font-size: 1.616em; }
h3 { font-size: 1.471em; }
h4 { font-size: 1.3em; }
h5 { font-size: 1.243em; }
h6 { font-size: 1.132em; }
p, a { 
	font-size: 1em; 
	margin: 0 0 1.5em 0;
	letter-spacing: -0.002em;
}
small { font-size: 0.769em; }

a { 
	color: inherit; 
	text-decoration-color: currentcolor;
} 
a.thick-underline { 
	display: inline-block;
	color: inherit; 
	text-transform: uppercase;	
	text-decoration: none;
	border-bottom: 0.2em solid #D89F31;
	line-height: 0.85em;
}
a.thick-underline::before {
	content: ">>";
}
a.thick-underline:hover,
a.thick-underline:focus {
  outline: none;
  background-color: #D89F31;
  color: #222;
}
hr {
	border: 0;
	width: 80vw;
	height: 1px;
	background-color: #eee;
}
img {
  width: 100%;
  height: auto;
}

/* REUSABLE CLASSES */

.flex-container-col {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
}
.flex-container-row {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
.spacer {
  background: transparent;
  height: 3em;
}

/* BUTTONS */

.btn {
	font-family: "Bree Serif", serif;
  color: inherit;
	font-size: 1.243em; 
	text-transform: uppercase;
	background-color: transparent;
	border: solid 2px #D89F31;
	border-radius: 2px;
	padding: 0.5em 3em;
  transition: all 0.1s;
}
.btn:hover, 
.btn:focus {
	cursor: pointer;
	background-color: #D89F31;
  outline: none;
	/*background-color: transparent;*/
}
.btn-small {
  font-size: 1em; 
  padding: 0.1em 0.5em;
}


/* SECTIONS */

main { 
	width: 100%; 
	min-height: 100vh;
}
.section-container { 
	width: 100%; 
}
.section-container.light { min-height: 70vh; }
.section-container.dark {
	padding-top: 3em;
	background-color: #222;
	color: #eee;
	clip-path: polygon(0 5vw, 100% 0, 100% 100%, 0% 100%);
  border-color: #eee;
}
section {
	margin: 3em 0;
	width: 90%;
	max-width: 800px;
}
.top-content {
	min-height: 80vh;
	margin-top: 20vh;
}

/* NAV */


nav {
	position: absolute;
	width: calc(100% - 1em);
	display: flex;
	margin: 0.5em;
}
nav a { margin: 0; }
#logo-img {
  width: auto;
	height: 4em;
/*	padding-top: 4em; */
/*	margin-top: -4em; */
	margin-left: 1em;
	margin-right: 1em;
  transition: all 0.1s ease;
}
#logo-img:hover,
#logo-container a:focus {
  transform: scale(1.1);
  outline: none;
}
#logo-container a {
  transition: all 0.3s ease;
}
/*
#logo-container:hover,
#logo-container:focus {
  transform: scale(1.1);
  outline: none;
}
*/
#nav-menu-list ul { z-index: 1; }
#nav-menu ul {
	display: flex;
	align-items: center;
}
#nav-menu li {
	display: inline;
	list-style-type: none;
	text-transform: uppercase;
}
/*#nav-menu input { display: none; }*/
#nav-menu-mobile-icon {
	position: relative;
	font-size: calc(20px + 2vw + 2vh);	
	line-height: 2em;
	margin-right: 0.5em;
	z-index: 2;
}
#nav-icon-mobile-open { display: inline; }
#nav-icon-mobile-close { display: none; }
#nav-menu-mobile-toggle { display: none; }
.nav-item {
	display: inline-block;
	margin: 0 1em 0 0;
}
.nav-item a { text-decoration: none; }
.nav-item a:hover,
.nav-item a:focus { 
	text-decoration: underline;
	text-decoration-thickness: 3px;
  outline: none;
}



/* BANNER */

section#banner { 
	max-width: 800px;  
  margin-top: 2vw;
	/*min-height: 60vh;*/
	/*display: flex;
	flex-direction: column;
	justify-content: center;*/
}
section#banner h1 { text-transform: none; }


/* PROJECTS */


.project-subsection {
  position: relative;
	align-items: flex-start;
	justify-content: space-between;
	flex-wrap: wrap;
	margin-bottom: 3em;
}
.project-subsection h5 {
  display: block;
  width: 100%;
}
.project-subsection-btn-container {
  /*position: absolute;
  top: 0;
  right: 0;*/
}
.project-subsection-img-container {
  display: block;
  width: 38%;
  margin-bottom: 1.5em;
}
.project-subsection-img-container a:hover,
.project-subsection-img-container a:focus {
  outline: none;
}
.project-subsection-img-container a {
  margin: 0;
}
.project-subsection-img {
	width: 100%;
  height: auto;
	min-width: 300px;
	max-width: 1024px;
}
.project-subsection-description {
	width: 55%;
	min-width: 300px;
	height: 100%;
}
.project-subsection-img.full-width,
.project-subsection-description.full-width {
	width: 100%;
}
.project-subsection-description.full-width {
	margin: 1em 0;
}


/* POSTS */

.post-banner-img {
  width: 100%;
  margin-bottom: 3em;
}

/* CONTACT FORM */


#contact-form label { 
	font-family: "Bree Serif", serif;
	font-size: 1.243em; 
	/*color: #eee;*/
	/*opacity: 1;*/
	text-transform: uppercase;
}
.contact-form-text,
.contact-form-textarea {
  color: inherit;
	background-color: transparent;
	width: calc(100% - 4px);
	margin-bottom: 1em;
	border: solid 1px;
	font-family: "Literata", serif; 
	font-size: 1em;
	/*opacity: 1;*/
	/*caret-color: #eee;*/
  border-color: currentColor;
  outline: none;
}
.contact-form-text.dark,
.contact-form-textarea.dark {
	border-color: #eee;
}
.contact-form-text {
	border-left: none;
	border-right: none;
	border-top: none;
}
.contact-form-textarea {
	resize: none;
}
.contact-form-submit {
	display: block;
	margin: auto;
}

/* FOOTER */


footer { 
	background-color: #AAA; 
	color: #fff; 
	padding: 0.5em;
}
#footer-container {
	width: 100%;
	max-width: 1140px;
	margin: auto;
	display: flex;
	align-items: center;
	justify-content: space-between;
}
#footer-container small {
	max-width: 50vw;
}
.footer-social-logo {
	margin: 0 0 0 0.3em;
  transition: all 0.3s;
}
.footer-social-logo:hover,
.footer-social-logo:focus {
  color: #222;
  outline: none;
}


/* MOBILE */

	#nav-menu-mobile-icon { display: none; }

@media 
	(-webkit-min-device-pixel-ratio: 1.25), 
	(min-resolution: 120dpi),
	(max-width: 875px) 
	{
		body { font-size: calc(20px + 0.2vw + 2vh); }
		nav { justify-content: space-between; }
		section, section#banner { max-width: none; }
		#logo-img { display: none; }
		#nav-menu-list { 
			display: none;
		}
		#nav-menu ul {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-content: center;
			flex-wrap: wrap;
			position: fixed;
			top: 0;
			left: 0;
			width: 100vw;
			height: 100vh;
			margin: 0;
			padding: 0;
			background-color: #ccc;
		}
		#nav-menu-mobile-icon { 
			display: block;
		}
		.nav-item {
			margin: 1em 1em;
		}
    .project-subsection a,
		.project-subsection-img,
		.project-subsection-description {
			width: 100%;
		}
		.project-subsection-description {
			/*margin: 1em 0;*/
		}
}
